.sidebar {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1000;
    width: 390px;
    height: 100%;
    background: white;
    box-shadow: 0 1px 5px 5px rgba(0, 0, 0, 0.3);
}

@media (max-width: 768px) {
    .sidebar {
        width: 100%;
        height: auto;
    }
}
