html, body, #root {
  height: 100%;
}

body {
  margin: 0;
  font-family: 'Roboto', 'Segoe UI', sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.8);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a, button, h1, h2, h3, h4, h5, h6, input, ol, p, textarea, th, ul {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

label:focus {
  outline: 0;
}
