.welfare-details p {
    margin: 0 0 10px 0;
}

.welfare-details__image {
    max-width: 100%;
    max-height: 200px;
    overflow: hidden;
}

.welfare-details__image img {
    max-width: 100%;
}

.welfare-details__title {
    margin: 20px;
    font-size: 28px;
    font-weight: bold;
}

.welfare-details__block {
    margin: 30px 20px;
}

.welfare-details__block--description {
    font-style: italic;
}

.welfare-details__block-title {
    font-weight: bold;
}
