.fulltext {
    background-color: #813689;
}

.fulltext__inputs {
    display: flex;
}

.fulltext__input-text {
    border: 0;
    background: transparent;
    color: #ffffff;
    font-size: 18px;
    padding: 10px 0;
    margin-left: 10px;
    width: 100%;
}

.fulltext__input-text:focus {
    outline: 0;
}

.fulltext__input-text::placeholder {
    color: rgba(255, 255, 255, 0.5);
}

.fulltext__button-back,
.fulltext__button-search,
.fulltext__button-clear {
    display: inline-block;
    padding: 10px;
}

.fulltext__button-back svg,
.fulltext__button-search svg,
.fulltext__button-clear svg {
    width: 20px;
    height: 20px;
    fill: #ffffff;
    cursor: pointer;
}

.fulltext__button-back input,
.fulltext__button-search input,
.fulltext__button-clear input {
    display: none;
}
