.category-search {
    list-style: none;
    margin: 0;
    padding: 0;
}

.category-search__category {
    display: flex;
    align-items: center;
    margin: 10px;
    padding: 10px;
    cursor: pointer;
    border-radius: 10px;
    font-weight: bold;
}

.category-search__category:hover {
    background: #f5f5f5;
}

.category-search__category-icon {
    display: flex;
    width: 50px;
    height: 50px;
    background: #813689;
    border-radius: 25px;
    align-items: center;
    justify-content: center;
    margin-right: 10px;
}

.category-search__category-icon svg {
    width: 30px;
    height: 30px;
    fill: #ffffff;
}
